    @import "~@/assets/styles/mobile_base.scss";
    .unreal-gotoHomeView {
        position: absolute;
        z-index: $z-index-index;
        left: 30px;
        top: 30px;
        width: 143.5px;
        height: 131px;
        transform: scale(1.2);
        background: url('~@/assets/img/mobile/pixelexit_normal.png');
        background-repeat: no-repeat;
        background-size: contain;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    .unreal-title-root{
        position: absolute;
        z-index: $z-index-index;
        left: 50%;
        transform: translate(-50%);
        width: 700px;
        height: 200px;        
    }
    .unreal-title{
        position: absolute;   
        top: -55px;        
        transform: scale(0.3);
        width: 700px;
        height: 200px;
        background: url('~@/assets/img/mobile/pixel-title.png');
